html,
body {
  height: 100%;
  width: 100%;
}

html,
body,
h1 {
  font-family: 'Noto Sans', sans-serif;
}

canvas {
  z-index: -1;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  min-width: 100%;
}

.masthead {
  display: table;
  width: 100vw;
  height: 100vh;
  text-align: center;
  color: white;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.masthead .intro-body {
  display: table-cell;
  vertical-align: middle;
  text-shadow: 2px 2px 13px #353535;
}

.masthead .intro-body .brand-heading {
  font-size: 50px;
}

.masthead .intro-body .intro-text {
  font-size: 18px;
  font-weight: bold;
}

@media (min-width: 768px) {
  .masthead {
    height: 100%;
    padding: 0;
  }
  .masthead .intro-body .brand-heading {
    font-size: 50px;
  }
  .masthead .intro-body .intro-text {
    font-size: 26px;
    font-weight: bold;
  }
}
