html,
body {
    height: 100%;
    width: 100%;
}

html,
body,
h1 {
    font-family: 'Noto Sans', sans-serif;
}

canvas {
    z-index: -1;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    min-width: 100%;
}

.masthead {
    display: table;
    width: 100vw;
    height: 100vh;
    // padding: 200px 0;
    text-align: center;
    color: white;
    // background: url('../img/vbase-banner.jpg') no-repeat bottom center scroll;
    //background-color: black;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    .intro-body {
        display: table-cell;
        vertical-align: middle;
        text-shadow: 2px 2px 13px #353535;
        .brand-heading {
            font-size: 50px;
        }
        .intro-text {
            font-size: 18px;
            font-weight: bold;
        }
    }
    @media(min-width:768px) {
        height: 100%;
        padding: 0;
        .intro-body {
            .brand-heading {
                font-size: 50px;
            }
            .intro-text {
                font-size: 26px;
                font-weight: bold;
            }
        }
    }
}